:root {
    --duration: 2s;
    --size: 50px;
}

.example {
    position: relative;
    margin: 100px auto;
    height: var(--size);
    width: var(--size);
    transform: translateX(-50%) translateY(-50%);
    z-index: 9999;
}

.container {
    position: absolute;
    top: 0;
    left: 0;
    height: var(--size);
    width: var(--size);
}

.block {
    position: absolute;
    top: 0;
    left: 0;
    height: var(--size);
    width: var(--size);
}

.block .item {
    position: absolute;
    height: var(--size);
    width: var(--size);
    background: #0061DF;
    animation: move var(--duration) linear infinite;
}

.block .item:nth-of-type(1) {
    top: calc(-1 * var(--size));
    left: calc(-1 * var(--size));
    animation-delay: calc(-7 * (var(--duration) / 8));
}

.block .item:nth-of-type(2) {
    top: calc(-1 * var(--size));
    left: 0;
    animation-delay: calc(-6 * (var(--duration) / 8));
}

.block .item:nth-of-type(3) {
    top: calc(-1 * var(--size));
    left: var(--size);
    animation-delay: calc(-5 * (var(--duration) / 8));
}

.block .item:nth-of-type(4) {
    top: 0;
    left: var(--size);
    animation-delay: calc(-4 * (var(--duration) / 8));
}

.block .item:nth-of-type(5) {
    top: var(--size);
    left: var(--size);
    animation-delay: calc(-3 * (var(--duration) / 8));
}

.block .item:nth-of-type(6) {
    top: var(--size);
    left: 0;
    animation-delay: calc(-2 * (var(--duration) / 8));
}

.block .item:nth-of-type(7) {
    top: var(--size);
    left: calc(-1 * var(--size));
    animation-delay: calc(-1 * (var(--duration) / 8));
}

.block .item:nth-of-type(8) {
    top: 0;
    left: calc(-1 * var(--size));
    animation-delay: 0s;
}

@keyframes move {
    0% {
        transform: rotate(0) scale(1);
        animation-timing-function: ease-in;
    }
    10% {
        transform: rotate(90deg) scale(0);
    }
    50% {
        transform: rotate(90deg) scale(0);
        animation-timing-function: ease-out;
    }
    60% {
        transform: rotate(180deg) scale(1);
    }
    100% {
        transform: rotate(180deg) scale(1);
    }
}
