@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

body {
  font-family: 'Montserrat', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* color */
  --primary-color: #0D263B;
  --secondary-color: #696969;

  /* background */
  --background-primary: #0061DF;

  /* padding */
  --padding: 0 130px;

  /* width */
  --width: 1440px;
}

.title {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 28px;
  line-height: 36px;
  letter-spacing: -2%;
  text-align: center;
  color: #0D263B;
}

.subTitle {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #0D263B;
}

.info {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #696969;
}

.ant-dropdown-menu  {
  margin-top: 15px !important;
}